import React from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Layout from "./Layout";
import { useFormik } from "formik";
import { useHistory } from "react-router-dom";

const noIpRegex = /[a-zA-z]/u;
const ipRegex = /^[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}$/u;
const rangeRegex = /^[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}-[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}$/u;

const ScanForm = (): JSX.Element => {
  const history = useHistory();
  const formik = useFormik({
    initialValues: { search: "" },
    onSubmit: ({ search }, { setSubmitting }) => {
      setSubmitting(false);
      history.push(
        noIpRegex.test(search) || search.includes("-")
          ? `/summary/${search}`
          : `/detail/${search}`
      );
    },
    validate: ({ search }) => {
      if (ipRegex.test(search) || rangeRegex.test(search)) {
        return {};
      }
      return { search: "currently just IP or IP range are supported." };
    },
  });

  return (
    <form onSubmit={formik.handleSubmit}>
      <Layout>
        <TextField
          name="search"
          id="search"
          label="ip or ip range"
          onChange={formik.handleChange}
          value={formik.values.search}
          error={
            Boolean(formik.values.search) &&
            formik.touched.search &&
            Boolean(formik.errors.search)
          }
          helperText={
            Boolean(formik.values.search) &&
            formik.touched.search &&
            formik.errors.search
          }
          fullWidth
        />

        <Button
          variant="contained"
          type="submit"
          disabled={formik.isSubmitting}
        >
          Scan
        </Button>
      </Layout>
    </form>
  );
};

export default ScanForm;
